.header {
  background-color: #ffffff;
  color: #111;
  padding: 40px 20px;
  font-family: "Inter", sans-serif;
}

.header__content {
  display: flex;
  flex-direction: column-reverse;
  gap: 40px;
  align-items: flex-start;
  justify-content: space-between;
}

@media (min-width: 768px) {
  .header__content {
    flex-direction: row;
    align-items: center;
  }
}

.header__text {
  flex: 1;
}

.header__title {
  font-size: 48px;
  font-weight: 700;
  line-height: 1.4;
  margin-bottom: 20px;
}

.header__description {
  font-size: 16px;
  color: #555;
  line-height: 1.6;
  margin-bottom: 24px;
}

.header__skills {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-bottom: 24px;
}

.header__skill {
  background-color: #f2f4f7;
  color: #444;
  padding: 6px 14px;
  font-size: 14px;
  border-radius: 999px;
}

.header__button {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  background-color: #1d8ae0;
  color: white;
  font-weight: 600;
  text-decoration: none;
  padding: 10px 18px;
  border-radius: 10px;
  transition: background-color 0.3s ease;
}

.header__button:hover {
  background-color: #1577c8;
}

.header__image-block {
  text-align: center;
}

.header__image {
  width: 210px;
  border-radius: 20px;
  object-fit: cover;
}

.header__caption {
  margin-top: 10px;
}

.header__name {
  font-size: 16px;
  font-weight: 600;
}

.header__role {
  font-size: 14px;
  color: #666;
}
