@import url('https://fonts.googleapis.com/css2?family=Lora:ital,wght@0,400..700;1,400..700&family=Manrope:wght@200..800&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&family=Unbounded:wght@200..900&display=swap');
* {
  font-family: "Manrope", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


.container {
  width: 90%;
  margin: 0 auto;
}


.nav ul {
  display: flex;
  list-style: none;
}

.nav li {
  margin-right: 20px;
}

.nav a {
  text-decoration: none;
  color: #333;
  font-weight: bold;
}

.main-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-top: 50px;
}

.intro {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}

.text-content h1 {
  font-size: 2rem;
  color: #333;
}

.text-content p {
  font-size: 1rem;
  color: #666;
}

.image-content img {
  width: 300px;
  height: auto;
  border-radius: 10px;
}

.telegram-link {
  display: flex;
  align-items: center;
  padding: 10px 20px;
  background-color: #000000; /* чёрный фон */
  color: #ffffff !important; /* белый текст */
  border-radius: 5px;
  text-decoration: none;
  font-size: 1rem;
  font-weight: bold;
  gap: 8px; /* добавляет немного расстояния между иконкой и текстом */
}
.telegram-link svg {
  color: #ffffff; /* делает иконку белой */
}

.telegram-link svg {
  margin-right: 10px;
}

@media screen and (max-width: 768px) {
  .intro {
    flex-direction: column;
    text-align: center;
  }

  .image-content img {
    width: 250px;
  }

  .text-content h1 {
    font-size: 1.5rem;
  }

  .text-content p {
    font-size: 0.9rem;
  }
}
.navbar{
  width: 100%;
}

.nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav ul {
  list-style: none;
  display: flex;
  gap: 20px;
  padding: 0;
  margin: 0;
}

.nav-left {
  justify-content: flex-start;
}

.nav-right {
  justify-content: flex-end;
}



