/* --- Base Styles & Desktop --- */
.navbar-container {
  position: relative; /* Needed for absolute positioning of mobile toggle */
  padding: 10px 20px;
}
/* Hide mobile elements on desktop */
.mobile-menu-toggle,
.mobile-nav {
  display: none;
}


/* --- Mobile Styles (max-width: 480px) --- */
@media (max-width: 480px) {

  /* Hide desktop navigation */
  .desktop-nav {
    display: none;
  }

  /* Show and style mobile menu toggle button */
  .mobile-menu-toggle {
    display: block; /* Show the button */
    position: absolute; /* Position it relative to navbar-container */
    top: 50%;
    right: 20px;
    transform: translateY(-50%); /* Center vertically */
    background: none;
    border: none;
    font-size: 1.8rem; /* Adjust size */
    color: #333; /* Icon color */
    cursor: pointer;
    z-index: 1001; /* Ensure it's above the closed curtain */
    padding: 5px;
  }

  /* Style the mobile navigation curtain */
  .mobile-nav {
    display: flex; /* Use flexbox for alignment */
    flex-direction: column;
    /* Align items to the start (left in LTR) based on screenshot */
    align-items: flex-start;
    justify-content: flex-start; /* Start content from the top */
    padding: 60px 30px 30px 30px; /* Add padding, more top padding for close button */
    box-sizing: border-box;

    position: fixed; /* Fixed position to cover the screen */
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh; /* Full viewport height */
    background-color: #2c2c2c; /* Dark background like screenshot */
    color: white;

    /* --- Animation --- */
    transform: translateY(-100%); /* Start hidden above the screen */
    transition: transform 0.4s ease-in-out; /* Smooth slide */
    z-index: 1000; /* Ensure it's above other content */
    overflow-y: auto; /* Allow scrolling if content overflows */
  }

  /* Style for when the mobile menu is open */
  .mobile-nav.open {
    transform: translateY(0); /* Slide into view */
  }

  /* Style the close button inside the mobile menu */
  .mobile-menu-close {
    position: absolute;
    top: 20px;
    right: 25px;
    background: none;
    border: none;
    font-size: 2.2rem; /* Make close icon larger */
    color: white;
    cursor: pointer;
    padding: 5px;
  }

  /* Style the list of links in the mobile menu */
  .mobile-nav-links {
    list-style: none;
    padding: 0;
    margin: 0;
    width: 100%; /* Take full width for alignment */
    margin-top: 30px; /* Space below close button */
  }

  .mobile-nav-links li {
    margin-bottom: 25px; /* Vertical spacing between links */
    width: 100%; /* Ensure links take width */
  }

  .mobile-nav-links li a {
    text-decoration: none;
    color: white;
    font-size: 1.4rem; /* Larger font size for mobile */
    display: flex; /* Use flex for icon alignment */
    align-items: center;
    gap: 8px; /* Space between text and icon */
    transition: color 0.2s ease;
    padding: 10px 0; /* Add some padding for easier tapping */
  }

   /* Specifically target telegram link for potential different styling if needed */
  .mobile-nav-links li a.telegram-link {
     /* You can override specific styles here if needed, */
     /* but inherits general 'a' styles above */
     /* Removing background for mobile version */
     background-color: transparent;
     padding: 10px 0; /* Reset padding if needed */
  }

  .mobile-nav-links li a:hover,
  .mobile-nav-links li a:active {
    color: #a0a0a0; /* Lighter gray hover for visibility */
  }
}