/* Section2 */
.section2 {
    background: #ffffff;
    padding: 48px 0;
  }
  
  .section2 .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 16px;
  }
  
  .section-title {
    font-size: 32px;
    font-weight: bold;
    margin-bottom: 32px;
  }
  
  .job-card {
    display: flex;
    flex-direction: column;
    gap: 24px;
  }
  
  @media (min-width: 768px) {
    .job-card {
      gap: 48px;
    }
  }
  
  .job-dates {
    font-size: 16px;
    color: #666;
    display: flex;
    gap: 8px;
    margin-bottom: 16px;
  }
  
  .job-main {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }
  
  @media (min-width: 768px) {
    .job-main {
      flex-direction: row;
      align-items: flex-start;
    }
  }
  
  .company-logo {
    width: 60px;
    height: 60px;
    object-fit: contain;
    margin-right: 16px;
  }
  
  .job-info {
    flex: 1;
  }
  
  .job-role {
    font-size: 20px;
    margin: 0 0 8px;
  }
  
  .company-desc {
    font-size: 14px;
    color: #444;
    margin-bottom: 16px;
  }
  
  .job-tasks {
    list-style: disc inside;
    font-size: 14px;
    color: #333;
    line-height: 1.6;
  }
  
  .subprojects {
    display: flex;
    flex-direction: column;
    gap: 24px;
  }
  
  @media (min-width: 768px) {
    .subprojects {
      flex-direction: row;
    }
  }
  
  .subproject {
    background: #f5f5f5;
    border-radius: 12px;
    padding: 16px;
    display: flex;
    align-items: center;
    gap: 16px;
    flex: 1;
  }
  
  .sub-left h4 {
    margin: 0 0 8px;
    font-size: 16px;
    font-weight: 600;
  }
  
  .sub-left p {
    margin: 0;
    font-size: 14px;
    color: #555;
  }
  
  .sub-img {
    width: 100px;
    border-radius: 8px;
    object-fit: cover;
  }
  


  /* style.css */

.section2-container {
  display: flex;
  gap: 30px; /* Расстояние между датой и основной информацией */
  padding: 20px; /* Добавьте отступы по необходимости */
  font-family: sans-serif; /* Используйте подходящий шрифт */
  max-width: 800px; /* Ограничьте максимальную ширину блока */
  margin: 20px auto; /* Центрирование и отступ сверху/снизу */
  line-height: 1.6;
}

.date-range {
  flex-shrink: 0; /* Предотвратить сжатие блока с датой */
  color: #666; /* Цвет текста даты */
  font-size: 0.9em;
  text-align: right; /* Выравнивание текста даты по правому краю */
  padding-top: 5px; /* Небольшой отступ сверху для выравнивания с заголовком */
  min-width: 100px; /* Минимальная ширина для блока даты */
}

.job-details {
  flex-grow: 1; /* Позволить этому блоку занимать оставшееся пространство */
}

.job-header {
  display: flex;
  align-items: flex-start; /* Выровнять лого и текст по верху */
  gap: 15px; /* Расстояние между логотипом и текстом */
  margin-bottom: 10px;
}

.company-logo {
  width: 50px; /* Размер логотипа */
  height: 50px;
  object-fit: contain; /* Сохранить пропорции логотипа */
  border-radius: 8px; /* Скругление углов как на картинке */
}

.title-block {
  display: flex;
  flex-direction: column;
}

.job-title {
  font-size: 1.2em;
  font-weight: bold;
  margin: 0 0 5px 0; /* Убрать отступ по умолчанию и добавить нижний */
  color: #333;
}

.company-description {
  font-size: 1em;
  color: #555;
  margin: 0; /* Убрать отступ по умолчанию */
}

.tags {
  margin-bottom: 15px;
  font-size: 0.9em;
  color: #777;
}

.tags span {
  background-color: #f0f0f0; /* Легкий фон для тегов */
  padding: 2px 6px;
  border-radius: 4px;
  margin-right: 5px;
}

.responsibilities {
  list-style: disc; /* Маркеры списка */
  margin-left: 20px; /* Отступ слева для списка */
  padding-left: 0; /* Убрать внутренний отступ по умолчанию */
  margin-bottom: 25px;
  color: #444;
}

.responsibilities li {
  margin-bottom: 8px; /* Расстояние между пунктами списка */
}

.resume-button {
  display: inline-block; /* Чтобы кнопка не занимала всю ширину */
  background-color: #282c34; /* Темный цвет кнопки как на картинке */
  color: white;
  padding: 12px 25px;
  text-decoration: none;
  border: none;
  border-radius: 25px; /* Сильно скругленные углы */
  cursor: pointer;
  font-size: 1em;
  font-weight: 500;
  text-align: center;
  transition: background-color 0.3s ease;
}

.resume-button:hover {
  background-color: #444a55; /* Цвет при наведении */
}

/* Медиа-запрос для мобильных устройств (опционально) */
@media (max-width: 600px) {
  .section2-container {
    flex-direction: column; /* Поменять направление на колонку */
    align-items: flex-start; /* Выровнять элементы по левому краю */
    gap: 15px;
  }

  .date-range {
    text-align: left; /* Выровнять дату по левому краю на мобильных */
    padding-top: 0;
    margin-bottom: 5px; /* Добавить небольшой отступ снизу */
    width: 100%; /* Занять всю ширину */
  }

  .job-header {
    /* Можно оставить как есть или тоже изменить при необходимости */
  }

   .resume-button {
     width: 100%; /* Растянуть кнопку на всю ширину */
     box-sizing: border-box; /* Учесть padding в ширине */
   }
}

.section2-container{
  padding-top: 90px;
}