/* styleFooter.css */

.site-footer {
    background-color: #282c34; /* Темный фон футера */
    color: #e0e0e0; /* Светлый цвет текста */
    padding: 40px 20px; /* Отступы */
    font-family: sans-serif; /* Шрифт */
  }
  
  .footer-content {
    display: flex;
    justify-content: space-between; /* Распределение колонок */
    flex-wrap: wrap; /* Позволить перенос колонок на маленьких экранах */
    max-width: 1200px; /* Ограничение ширины контента футера */
    margin: 0 auto; /* Центрирование */
    gap: 30px; /* Пространство между колонками */
  }
  
  .footer-column {
    flex: 1; /* Позволить колонкам растягиваться */
    min-width: 200px; /* Минимальная ширина колонки перед переносом */
    padding: 0 10px; /* Небольшие отступы внутри колонок */
  }
  
  .footer-title {
    font-size: 1.1em;
    font-weight: 600;
    margin-bottom: 15px;
    color: #ffffff; /* Белый цвет для заголовков */
    display: flex;
    align-items: center;
    gap: 8px;
  }
  
  .title-avatar-icon {
    width: 24px;
    height: 24px;
    border-radius: 50%; /* Сделать иконку круглой, если нужно */
  }
  
  .qr-code-image {
    width: 130px; /* Размер QR кода */
    height: 130px;
    background-color: white; /* Белый фон для QR, если он прозрачный */
    padding: 5px; /* Небольшой отступ вокруг QR */
    border-radius: 8px; /* Скругление углов */
  }
  
  .footer-list {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .footer-list li {
    display: flex;
    align-items: center;
    margin-bottom: 12px; /* Расстояние между пунктами */
    gap: 10px; /* Расстояние между иконкой и текстом */
  }
  
  .footer-list a {
    color: #e0e0e0; /* Цвет ссылок */
    text-decoration: none;
    font-size: 0.95em;
    transition: color 0.2s ease;
  }
  
  .footer-list a:hover {
    color: #ffffff; /* Цвет ссылок при наведении */
    text-decoration: underline; /* Подчеркивание при наведении */
  }
  
  .list-icon {
    width: 18px; /* Размер иконок в списках */
    height: 18px;
    object-fit: contain; /* Сохранить пропорции иконки */
  }
  
  .case-icon {
      width: 16px; /* Можно задать другой размер для иконок кейсов */
      height: 16px;
      border-radius: 3px; /* Небольшое скругление для иконок кейсов, если нужно */
  }
  
  
  /* Колонка с кнопкой "Наверх" */
  .scroll-top-column {
    flex-grow: 0; /* Не растягивать эту колонку */
    flex-shrink: 0;
    flex-basis: auto; /* Базовая ширина по контенту */
    display: flex;
    justify-content: flex-end; /* Выровнять кнопку по правому краю */
    align-items: flex-start; /* Выровнять кнопку по верху */
    min-width: auto; /* Убрать min-width для этой колонки */
  }
  
  .scroll-top-button {
    background-color: #4a4f58; /* Фон кнопки */
    border: none;
    border-radius: 50%; /* Круглая кнопка */
    width: 40px;
    height: 40px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background-color 0.2s ease;
  }
  
  .scroll-top-button img {
    width: 18px; /* Размер стрелки */
    height: 18px;
  }
  
  .scroll-top-button:hover {
    background-color: #61666f; /* Фон при наведении */
  }
  
  /* Адаптивность */
  @media (max-width: 992px) {
    .footer-content {
      justify-content: flex-start; /* Начать выравнивание слева */
    }
     .footer-column {
      flex-basis: 45%; /* Примерно 2 колонки в ряд */
      min-width: 250px; /* Увеличить минимальную ширину для 2-х колонок */
    }
     .scroll-top-column {
      /* На средних экранах можно переместить ее */
      order: -1; /* Поставить первой */
      flex-basis: 100%; /* Занять всю ширину */
      justify-content: flex-end; /* Кнопку вправо */
      align-items: center; /* Выровнять по центру */
      margin-bottom: 20px; /* Добавить отступ снизу */
      padding-right: 20px;
    }
  }
  
  
  @media (max-width: 600px) {
    .footer-content {
      flex-direction: column; /* Поставить колонки друг под другом */
      align-items: center; /* Центрировать колонки */
      text-align: center; /* Центрировать текст внутри колонок */
    }
  
    .footer-column {
      flex-basis: 100%; /* Одна колонка на всю ширину */
      min-width: unset; /* Сбросить минимальную ширину */
      align-items: center; /* Центрировать контент внутри колонки */
      text-align: center;
    }
  
    .footer-title {
      justify-content: center; /* Центрировать заголовок с иконкой */
    }
  
    .footer-list li {
      justify-content: center; /* Центрировать элементы списка */
    }
  
    .qr-column {
        display: flex;
        flex-direction: column;
        align-items: center; /* Явно центрировать QR */
    }
  
    .scroll-top-column {
        order: 1; /* Поставить последней на мобильных */
        margin-top: 20px; /* Добавить отступ сверху */
        width: 100%;
        justify-content: center; /* Центрировать кнопку */
        padding: 0;
    }
    .scroll-top-button {
        /* Можно немного увеличить кнопку на мобильных */
        /* width: 45px; */
        /* height: 45px; */
    }
  }